import React, {Component} from 'react';

class Table extends Component {
	render() {
		const {students, mentorGithub} = this.props;
		const tasks = [];

		const studentsList = [];
		for (const student in students) {
			if (students.hasOwnProperty(student) && students[student].github !== undefined) {
				studentsList.push(
					<th key={student.github}><a target={'_blank'} href={'https://github.com/' + student}>{student}</a></th>
				);

				if (tasks.length === 0) {
					for (const task in students[student].tasks) {
						if (students[student].tasks.hasOwnProperty(task)) {
							tasks.push( {name: task, link: students[student].tasks[task].link} );
						}
					}
				}
			}
		}


		const tasksList = [(<tr key={'students'}><td />{studentsList}</tr>)];

		tasks.forEach((task) => {
			tasksList.push(
				<tr key={task.name}>
					<a href={task.link} target={'_blank'}><td>{task.name}</td></a>
					{markTaskStatus(task.name)}
				</tr>
			);
		});


		function markTaskStatus(task) {
			const markedData = [];

			for (const student in students) {
				if (students.hasOwnProperty(student)) {
					const status = students[student].tasks[task].status;

					let color;
					switch (status) {
					case('Checked'): {
						color = '#81EF99';
						break;
					}
					case('Checking'): {
						color = '#CC9494';
						break;
					}
					case('In Progress'): {
						color = '#FCFF6C';
						break;
					}
					case('Missed'): {
						color = '#8C3C3C';
						break;
					}
					case('ToDo'): {
						color = '#C0C1BF';
						break;
					}
					}

					markedData.push(
						<td style={{backgroundColor: color}} />
					);
				}
			}

			return markedData;
		}


		return (
			<div className='table'>
				<table>
					<caption><a target={'_blank'} href={'https://github.com/' + mentorGithub}>{mentorGithub}</a></caption>
					<tbody>
						{tasksList}
					</tbody>
				</table>
			</div>
		);
	}
}

export default Table;
