import React, {Component} from 'react';
import Select from 'react-select';
import Table from './Table';
import {render} from 'react-dom';


class SelectMentor extends Component {
	constructor(props) {
		super(props);

		this.state = {
			selectedOption: null,
		};

		this.handleChange = (selectedOption) => {
			const {mentors} = this.props;
			this.setState({ selectedOption });
			localStorage.setItem('mentor', selectedOption.value);
			render(<Table mentorGithub={selectedOption.value} students={mentors[selectedOption.value].students}/>,
				document.querySelector('.table'));
		};
	}

	render() {
		const {mentors} = this.props;
		const mentorsList = [];
		for (const property in mentors) {
			if (mentors.hasOwnProperty(property) && mentors[property].github !== undefined) {
				mentorsList.push( {value: mentors[property].github, label: mentors[property].github} );
			}
		}

		const {selectedOption} = this.state;

		return (
			<div className='select-mentor'>
				<h3>Choose mentor: </h3>
				<Select onChange={this.handleChange} value={selectedOption} options={mentorsList} />
			</div>
		);
	}
}

export default SelectMentor;
