import React, {Component} from 'react';
import SelectMentor from './SelectMentorForm';
import {render} from 'react-dom';
import Table from './Table';
import Colors from './Colors';

class App extends Component {
	render() {
		const {mentors} = this.props;

		if (localStorage.mentor) {
			render(<Table mentorGithub={localStorage.mentor} students={mentors[localStorage.mentor].students}/>,
				document.querySelector('.table'));
		}

		render(<Colors />, document.querySelector('.colors'));

		return (
			<div>
				<SelectMentor mentors={mentors}/>
			</div>
		);
	}
}

export default App;
